body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  font-family: Calibri, sans-serif;
  background-color: #F8FAFC;
}

.form-container {
  width: 400px;
  border-radius: 0.75rem;
  background-color: #BCCCDC;
  padding: 2rem;
  color: #333333;
}

.title {
  text-align: center;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
  color: #333333;
  margin-bottom: 1.5rem;
}

.form {
  margin-top: 1.5rem;
}

.input-group {
  margin-top: 0.25rem;
  font-size: small;
  line-height: 1.25rem;
  width: 100%;
  margin-bottom: 1rem;
}

.input-group label {
  display: block;
  color: #333333;
  margin-bottom: 4px;
  font-weight: bold;
  font-size: large;
}

.input-group input {
  width: 100%;
  border-radius: 0.375rem;
  border: 1px solid #9AA6B2;
  outline: 0;
  background-color: #F8FAFC;
  padding: 0.75rem 1rem;
  color: #333333;
  box-sizing: border-box;
}

.input-group input:focus {
  border-color: #9AA6B2;
}

.radio-input {
  position: relative;
  display: flex;
  align-items: center;
  border-radius: 0.375rem;
  background-color: #D9EAFD;
  color: #333333;
  width: 100%;
  overflow: hidden;
  border: 1px solid #9AA6B2;
}

.radio-input label {
  flex: 1;
  padding: 0.75rem 1rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  font-weight: 600;
  font-size: 14px;
  transition: color 0.3s ease;
}

.radio-input input[type="radio"] {
  display: none;
}

.selection {
  position: absolute;
  height: 100%;
  width: 33.33%;
  z-index: 0;
  left: 0;
  top: 0;
  transition: transform 0.3s ease;
  background-color: #9AA6B2;
}

.radio-input input[type="radio"]:checked + label {
  color: #fff;
}

.radio-input input[type="radio"]:nth-of-type(1):checked ~ .selection {
  transform: translateX(0);
}

.radio-input input[type="radio"]:nth-of-type(2):checked ~ .selection {
  transform: translateX(100%);
}

.radio-input input[type="radio"]:nth-of-type(3):checked ~ .selection {
  transform: translateX(200%);
}

.btn-12 {
  background-color: #9AA6B2;
  color: #333333;
  font-weight: 700;
  margin: 1.5rem 0;
  border-radius: 0.375rem;
  padding: 0.8rem 3rem;
  width: 100%;
  display: block;
  text-align: center;
  text-transform: uppercase;
  transition: background-color 0.2s ease;
  border: none;
  cursor: pointer;
}

.btn-12:hover {
  background-color: #F8FAFC;
}

.form-container-predicted-price {
  width: 100%;
  border-radius: 0.75rem;
  background-color: #F8FAFC;
  padding: 1rem;
  color: #333333;
  margin-top: 1rem;
  text-align: center;
  box-sizing: border-box;
  border-radius: 0.375rem;
  border: 1px solid #9AA6B2;


}

.form-container-predicted-price .title {
  font-weight: bold;
}
